import * as React from 'react';
import {StyleSheet, View} from 'react-native';
import {useSafeArea} from '../lib/SafeArea';
import {getItem} from '../lib/Yeet';
import FeedTabView from './FeedTabView';
import {SCREEN_DIMENSIONS} from './ScreenSize';
import {ListClicker} from './ListClicker';
import {PullyScrollViewContext} from './PullyView';
import Animated from 'react-native-reanimated';
import {CloseButtonImage} from './CloseButtonImage';

const {height, width} = SCREEN_DIMENSIONS;

const styles = StyleSheet.create({
  header: {},
  clicker: {
    position: 'absolute',
    left: 16,
  },
});

export const FeedSheet = () => {
  const {top, bottom} = useSafeArea();
  const {snapSheet, position} = React.useContext(PullyScrollViewContext);

  const dismiss = React.useCallback(() => {
    console.log('DISMISS');
    return snapSheet('bottom');
  }, [snapSheet]);

  return (
    <View
      style={{
        height: height - top - bottom,
        width,
      }}>
      <View
        style={{
          // marginTop: top,
          shadowColor: 'black',
          shadowRadius: 2,
          shadowOpacity: 0.25,
          height: height - top - bottom,
          width,
        }}>
        <View
          style={{
            height: height - top - bottom,
            width,
            overflow: 'hidden',
            borderRadius: 12,
            position: 'relative',
          }}>
          <FeedTabView
            scrollY={this.scrollY}
            scrollEnabled
            offset={0}
            headerHeight={0}
            height={height - top - bottom - 48}
            initialRoute={getItem('FEED_SHEET_INITIAL_ROUTE', 'string')}
            width={width}
          />

          <ListClicker
            onPress={dismiss}
            style={[
              styles.clicker,
              {bottom: bottom + 16, zIndex: 999, width: 48, height: 48},
            ]}>
            <Animated.View>
              <CloseButtonImage />
            </Animated.View>
          </ListClicker>
        </View>
      </View>
    </View>
  );
};
