import * as React from 'react';
import {Platform} from 'react-native';
import {RNLocation, Location} from '../lib/Location';
import {getLastLocation, getLocationStatus} from '../lib/Yeet';
import {LocationPermissionStatus} from 'react-native-location';

RNLocation.configure({
  distanceFilter: 100, // Meters
  desiredAccuracy: {
    ios: 'best',
    android: 'balancedPowerAccuracy',
  },
  // Android only
  androidProvider: 'auto',
  interval: 5000, // Milliseconds
  fastestInterval: 10000, // Milliseconds
  maxWaitTime: 5000, // Milliseconds
  // iOS Only
  activityType: 'other',
  allowsBackgroundLocationUpdates: false,
  headingFilter: 1, // Degrees
  headingOrientation: 'portrait',
  pausesLocationUpdatesAutomatically: false,
  showsBackgroundLocationIndicator: false,
});

type LocationWithStatus = Location & {
  locationStatus: LocationPermissionStatus;
};
type UserLocation = LocationWithStatus & {
  canRequestLocationAccess: Boolean;
  setCanRequestLocationAccess: (enabled: boolean) => void;
};

export const UserLocationContext = React.createContext<UserLocation>(null);
export const hasLocation = (location: UserLocation) =>
  !!(location && location.latitude && location.longitude);

export const UserLocationProvider = ({children}) => {
  const [
    canRequestLocationAccess,
    setCanRequestLocationAccess,
  ] = React.useState(
    Platform.select({
      ios: true,
      web: false,
      android: true,
    }),
  );

  const [location, setLocation] = React.useState<LocationWithStatus>(() => {
    const _location = getLastLocation();
    const locationStatus: LocationPermissionStatus =
      getLocationStatus() ?? 'notDetermined';

    if (_location) {
      return {
        ..._location,
        locationStatus: 'authorizedWhenInUse',
      } as LocationWithStatus;
    } else {
      return {
        latitude: null,
        longitude: null,
        locationStatus,
      } as LocationWithStatus;
    }
  });

  const locationUnsubscriber = React.useRef(null);

  React.useEffect(() => {
    console.log('Requesting...', canRequestLocationAccess);
    if (!canRequestLocationAccess) {
      return;
    }

    const unsubscribe = RNLocation.subscribeToLocationUpdates(locations => {
      const _location = locations[0];

      if (Platform.OS !== 'web') {
        _location.locationStatus = getLocationStatus();
      }

      setLocation(_location);
    });

    return () => unsubscribe();
  }, [canRequestLocationAccess, setLocation]);
  const _setLocation = React.useRef(setLocation);
  _setLocation.current = setLocation;

  const contextValue = React.useMemo(
    () => ({
      ...location,
      canRequestLocationAccess,
      setCanRequestLocationAccess,
      setLocation: _setLocation,
    }),
    [
      canRequestLocationAccess,
      setCanRequestLocationAccess,
      location,
      _setLocation,
    ],
  );

  return (
    <UserLocationContext.Provider value={contextValue}>
      {children}
    </UserLocationContext.Provider>
  );
};
